import distanceFromLatLon from "../common/distanceFromLatLon";
import _ from 'lodash';

let cities;

export default (lat2, lon2, limitBy, maxMiles, orderBy) => $.get("data/usacitiespop.json").then(json=>{
    var regions = [];
    
    for (const item of json){
        const [city, region, popInt, lat1, lon1] = item;
        const {miles} = distanceFromLatLon(parseFloat(lat1), parseFloat(lon1), lat2, lon2); 

        if (miles > maxMiles) continue;
        
        regions.push({
            city: _.capitalize(city),
            region, 
            popInt, 
            distMiles: miles});
    };

    var sortKey = (['popInt', 'distMiles'])[orderBy];

    switch (orderBy){
        case 0:{
            return _.take(_.reverse(_.sortBy(regions, sortKey)), limitBy);
        }
        case 1:{
            return _.take(_.sortBy(regions, sortKey), limitBy);
        }
    }
});
