import degreeToRadians from "./degreeToRadians";
import milesFromKilometers from "./milesFromKilometers";

export default (lat1,lon1,lat2,lon2) => {
  const R = 6371; // Radius of the earth in km
  const dLat = degreeToRadians(lat2-lat1);  // deg2rad below
  const dLon = degreeToRadians(lon2-lon1); 
  const a = Math.sin(dLat/2) * Math.sin(dLat/2) + Math.cos(degreeToRadians(lat1)) * Math.cos(degreeToRadians(lat2)) * Math.sin(dLon/2) * Math.sin(dLon/2); 
  const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a)); 
  const km = R * c; // Distance in km
  
  return {
      km: km,
      miles: Math.round(milesFromKilometers(km))
  };
}
